.cus-back{
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      padding: 20px 0px;
      z-index: 5;
}
.cus-back__btn{
      padding: 8px 16px;
      border: solid 1px var(--purple);
      outline: none;
      cursor: pointer;
      font-size: 14px;
      line-height: 20px;
      font-style: 700;
      color: var(--purple);
      display: flex;
      align-items: center;
      background-color: transparent;
      border-radius: 20px;
      transition: all 0.3s;
      z-index: 5;
}
.cus-back__btn:hover{
      opacity: 0.6;
}
/* res-mobile */
/* Mobile */
@media only screen and (max-width: 767px){
      .cus-back{
            padding-top: 18px;
      }
      .cus-back__btn{
            border: none;
      }
}